import axios from 'axios';

import api from './api';

export function getCurrentList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getCurrentList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}




