import MyIndex from './index.vue' //引入公共组件
import Msg from './do-msg.vue'
import List from './list'

const components = {
    install: function(Vue){
        Vue.component('Index', MyIndex);    //挂载
        Vue.component('doMsg', Msg)
        Vue.component('List', List)
    }
}
export default components;


