<template>
    <div>
        <account-info :id="$route.query.id" :type="currency"></account-info>
        <div class="field-part">
            <mt-cell title="定期类型:" v-model="infoData.type" @click.native="showTypeSheet = true" is-link></mt-cell>
            <mt-cell title="存期:" v-model="infoData.term" @click.native="showTermSheet = true" is-link></mt-cell>
            <mt-cell title="执行利率:" v-model="infoData.rate"></mt-cell>
            <mt-field label="交易金额:" v-model="infoData.ammount" type="text"></mt-field>
            <mt-field label="交易密码:" v-model="infoData.password" type="password"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="default" plain size="small" @click="$back">返回</mt-button>
            <mt-button type="primary" size="small" @click="doConfirm()">确定</mt-button>
        </div>
        <!--    存期弹出框    -->
        <mt-actionsheet :actions="termListSheet" v-model="showTermSheet"></mt-actionsheet>
        <!--    转存标志弹出框    -->
        <mt-actionsheet :actions="typeListSheet" v-model="showTypeSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Actionsheet } from 'mint-ui-rem-dpr';
import AccountInfo from '../../account-info'
export default {
    components: {
        AccountInfo
    },
    data() {
        return {
            infoData: {
                term: '三个月',
                rate: '1.4300%',
                type: '开户',
                ammount: '',
                password: ''

            },
            currency: '人民币',
            termListSheet: [{
                name: '三个月',
                val: '1.4300%',
                method: this.termSheetCallback
            }, {
                name: '六个月',
                val: '1.584%',
                method: this.termSheetCallback
            }, {
                name: '一年',
                val: '1.699%',
                method: this.termSheetCallback
            }],
            typeListSheet: [{
                name: '开户',
                method: this.signSheetCallback
            }, {
                name: '不转存',
                method: this.signSheetCallback
            }],
            showTermSheet: false,
            showTypeSheet: false
        }
    },
    methods: {
        doConfirm() {
            if (this.$validate.isEmpty(this.infoData.ammount)) {
                this.$msg('交易金额不能为空。', 'error', 1000);
                return false;
            }

            if (!this.$validate.isAmount(this.infoData.ammount)) {
                this.$msg('交易金额格式错误。', 'error', 1000);
                return false
            }

            if (this.$validate.isEmpty(this.infoData.password)) {
                this.$msg('交易密码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.infoData.password)) {
                this.$msg('交易密码格式错误。', 'error', 1000);
                return false;
            }
            this.$router.push({ name: 'currentRegularMsg', params: { go: -3 } })
        },
        termSheetCallback(item) {
            this.infoData.term = item.name;
            this.infoData.rate = item.val;
        },
        signSheetCallback(item) {
            this.infoData.type = item.name;
        }
    },
    created() {

    }
}

</script>



