<template>
    <div id="current">
        <div class="field-part">
            <mt-cell title="选择一卡通" v-model=" dataForm.tranAccount " @click.native=" showAccountSheet = true " is-link></mt-cell>
        </div>
    
        <div class="field-part">
            <mt-cell title="选择子账户" v-model=" dataForm.tranAccountChild " @click.native=" showChildAccountSheet = true " is-link></mt-cell>
            <mt-cell title="时间跨度" v-model=" dataForm.timeLength ">
                <span v-for="item in timeTags" :class="{'active': activeTags === item.type}" :key="item.type" class="btn" @click="timeSelsct(item.type)">{{item.name}}</span>
            </mt-cell>
            <mt-cell title="开始日期" @click.native="open('startTimePicker')" :value=" dataForm.startDate | date('%Y-%m-%d')"></mt-cell>
            <mt-cell title="结束日期" @click.native="open('endTimePicker')" :value=" dataForm.endDate | date('%Y-%m-%d') "></mt-cell>
        </div>
    
        <div class="field-action">
            <mt-button type="primary" size="large" @click="query()">查询</mt-button>
        </div>
        <!--    开始日期弹出框    -->
        <mt-datetime-picker ref="startTimePicker" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日" v-model="startTimevalue" @confirm="getTime('startTimevalue', 'startDate')" :start-date="minDate" :end-date="stratEndDate">
        </mt-datetime-picker>
        <!--    结束日期弹出框    -->
        <mt-datetime-picker ref="endTimePicker" type="date" year-format="{value} 年" month-format="{value} 月" date-format="{value} 日" v-model="endTimeValue" @confirm="getTime('endTimeValue', 'endDate')" :start-date="endStartDate" :end-date="maxDate">
        </mt-datetime-picker>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet" v-model="showAccountSheet"></mt-actionsheet>
        <!--    子账户弹出框    -->
        <mt-actionsheet :actions="accountChildListSheet" v-model="showChildAccountSheet"></mt-actionsheet>
    
    </div>
</template>
<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
//获取一卡通账户
import * as serviceAccount from '../../../services/account';
export default {
    name: 'current',
    data() {
        return {
            dataForm: {
                tranAccountChild: '',
                tranAccountChildId: '',
                timeLength: '',
                startDate: '',
                endDate: '',
                tranAccountId: '',
                tranAccount: ''
            },
            timeTags: [{
                type: "week",
                name: "一周内"
            }, {
                type: "month",
                name: "一月内"
            }, {
                type: "year",
                name: "一年内"
            }],
            activeTags: "week",
            accountListSheet: [],
            accountChildListSheet: [],
            showAccountSheet: false,
            showChildAccountSheet: false,
            startTimevalue: null,
            endTimeValue: null,
            stratEndDate: '',
            endStartDate: ''
        }
    },
    computed: {
        maxDate() {
            return new Date();
        },
        minDate() {
            let dateYear = new Date();
            dateYear.Year(dateYear, 5);
            return dateYear;
        }
    },
    methods: {
        open(picker) {  //弹出时间选择框
            this.$refs[picker].open();
        },
        getTime(value, type) {   //获取选择时间
            let _copy = this.dataForm[type];
            this.dataForm[type] = this[value];
            let start = this.dataForm.startDate.toLocaleDateString();
            let end = this.dataForm.endDate.toLocaleDateString();
            if (this.$compare(start, end, '开始日期不能大于结束日期')) {
                this.dataForm[type] = _copy;
            }
            this.endStartDate = this.dataForm.startDate;
            this.stratEndDate = this.dataForm.endDate;

        },
        timeSelsct(type) {
            this.activeTags = type;
            let dateEnd = new Date();
            let dateStart = new Date();
            if(type === 'week') {
                this.dataForm.startDate = dateStart.Week(dateStart, 1);
            }
            else if(type === 'month') {
                this.dataForm.startDate = dateStart.Month(dateStart, 1);
            }
            else if(type === 'year') {
                this.dataForm.startDate = dateStart.Year(dateStart, 1);
            }
            this.dataForm.endDate = dateEnd;
            this.startTimevalue = this.dataForm.startDate;
            this.endTimeValue = this.dataForm.endDate;
            this.endStartDate = this.dataForm.startDate;
            this.stratEndDate = this.dataForm.endDate;
        },
        query() {
            this.$router.push('current-list');
        },
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
        },
        accountChildCallback(item) {
            this.dataForm.tranAccountChild = item.name;
            this.dataForm.tranAccountChildId = item.id;
        },

    },
    created() {
        this.timeSelsct("week");

        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
        serviceAccount.getAccountChildList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccountChild) {
                this.dataForm.tranAccountChild = _firstAccount.account;
                this.dataForm.tranAccountChildId = _firstAccount.id;
            }

            let _copy = [];
            res.data.list.forEach((item, index) => {
                _copy.push({
                    id: item.id,
                    name: item.account,
                    method: this.accountChildCallback,
                })
            });

            this.accountChildListSheet = _copy;
        }, (err) => {
            Toast(err);
        });
        this.$store.dispatch('showLoading', false);

    }

}
</script>

<style lang="scss" scoped>
@import '../../../assets/styles/mixins';
.btn {
    padding: 12px;
    margin: auto 8px;
    color: #2196F3;
    border: 1px solid #2196F3;
    border-radius: 3px;
}

.active {
    color: #fff;
    background-color: #2196F3;
}
</style>


