<template>
    <div class="account-name-doEdit">
        <mt-cell title="客户名称:"
                 :value="infoData.account_name_copy"></mt-cell>
        <mt-cell title="证件类型:"
                 v-model="infoData.certificates_type"></mt-cell>
        <mt-cell title="证件号码:"
                 v-model="infoData.certificates_No"></mt-cell>
        <mt-cell title="客户风险等级:"
                 v-model="infoData.grade"></mt-cell>
        <mt-cell title="风险评级有效日期:"
                 v-model="infoData.toDate"></mt-cell>
        <mt-cell title="手机号码:"
                 v-model="infoData.call"
                 type="textarea"></mt-cell>
    
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$router.go(-2)">返回</mt-button>
        </div>
    
    </div>
    
    </div>
</template>

<script>
import * as serviceAccount from '../../../services/account';
export default {
    data() {
        return {
            infoData: {
                certificates_type: '',
                account_name_copy: '',
                certificates_No: '',
                grade: '',
                toDate: '',
                call: ''

            }
        };
    },
    methods: {
        getData() {
            return new Promise((resolve, reject) => {
                serviceAccount.getAccountList().then((res) => {
                    if (!res.data.status) {
                        Toast(res.data.message);
                        return;
                    }
                    resolve(res.data.list);
                }, (err) => {
                    reject(err);
                });
            });
        }
    },
    created() {
        this.getData().then((data) => {
            let self = this;
            let _data_copy = data.filter( (item) => {
                return item.account == self.$route.params.account;
            });
            this.infoData = _data_copy[0];
            this.$store.dispatch('showLoading', false);
        }, (data) => {
            Toast(data);
        });
    }
};
</script>



