<template>
    <div>
        <div class="reminder">
            <p class="reminder-title">温馨提示：</p>
            <p>请先仔细阅读然后填写《个人投资风险承受能力评估表》。</p>
            <p>1.以下一系列问题可在您选择金融产品前，协助您评估自身的风险承受能力、理财方式及投资目标。</p>
            <p>2.您须认知、了解并同意，如您提供不准确或不完成资料，或选择不提供特定资料，则可能对您投资风险承受能力的评估及金融产品推荐带来影响，我行无任何责任。</p>
            <p>3.理财非存款，产品有风险，投资需谨慎。投资者在选择金融产品时须遵守具体产品的规定及条件，同时还需充分考虑投资可能面临的市场风险、汇率风险、信用风险以及可能的本金损失等风险。</p>
            <p>以下问题将根据您的财务状况、投资经验、投资偏好和风险承受能力对您进行风险评估，我们将根据评估结果为您更好的配置资产，请您认真作答，感谢您的配合！（每个问题请选择唯一选项，不可多选）</p>
        </div>
        <div class="field-part">
            <mt-cell title="风险评估:"></mt-cell>
        </div>
        <div class="field-part"
             v-for="item in radioData">
            <mt-radio :title="item.title"
                      v-model="item.value"
                      :options="item.options">
            </mt-radio>
        </div>
        <div class="field-part">
            <mt-field label="密码"
                      placeholder="请输入密码"
                      type="password"
                      v-model="password"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doSubmit()">提交</mt-button>
    
        </div>
        <div class="reminder">
            <p>本人保证上述所填信息为本人真实的意思表述，完全独立依据自身情况和判断做出上述答案，并接受贵行评估意见。同时确认如本人发生可能影响自身风险承受能力的情形，再次购买金融产品时必须主动要求银行重新对本人进行风险承受能力评估。否则由此导致的一切后果由本人承担。</p>
        </div>
    </div>
</template>

<script>

export default {
    data() {
        return {
            password: '',
            radioData: [{
                title: '1、您的年龄是多少？',
                value: '',
                options: ['60以上', '45到60', '30到45', '30以下']
            }, {
                title: '2、您的主要收入组成为？',
                value: '',
                options: ['退休或待岗在家', '基本工资为生', '提出为主', '年终奖和额外薪酬激励']
            }, {
                title: '3、您目前的家庭经济负担是以下哪种情况？',
                value: '',
                options: ['单薪有子', '双薪有子', '双薪无子', '无负担']
            }]
        }
    },
    methods: {
        doSubmit() {
            let _value = [];
            let flag = 0;
            this.radioData.forEach((item) => {
                _value.push(item.value);
            });
            _value.forEach((item) => {
                if (this.$validate.isEmpty(item)) {
                    flag++;
                }
            })
            if (flag > 0) {
                this.$msg('您的问题还没有做完！', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(this.password)) {
                this.$msg('交易密码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.password)) {
                this.$msg('交易密码格式错误。', 'error', 1000);
                return false;
            }
            this.$router.push({name: 'financialProductsReassessmentResult'})
        }
    }
}

</script>




