<template>
    <div>
        <div class="field-part">
            <mt-cell title="信用卡姓名" value="彭宇"></mt-cell>
            <mt-cell title="信用卡证件号码" value="342303********33456"> </mt-cell>
            <span>温馨提示 证件暂时不可修改</span>
        </div>
        <div class="field-action">
            <mt-button type="default"  @click="$back()">返回</mt-button>
        </div>
    </div>


</template>

<script>
    export default {
        data (){
            return {
                dataForm: {
                    name: '',
                    line: '',
                    account: ''
                },
            }
        },
        created() {
            this.dataForm = Object.assign({}, this.dataForm, this.$route.params.info );
            if("625228***0089"==this.dataForm.account) {
                this.dataForm.name="郭振宇";
                this.dataForm.line="20000";
            }else if ("625228***0025"==this.dataForm.account){
                this.dataForm.name="郭振宇2";
                this.dataForm.line="30000";
            }else {
                this.dataForm.name="郭振宇3";
                this.dataForm.line="40000";
            }
        }

    }
</script>

<style lang="scss" scoped>

    @import '../../assets/styles/mixins';

</style>



