<template>
    <div>
        <div class="field-part">
            <mt-cell title="产品购买:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="理财账户"
                     :value="productsList.account"
                     is-link></mt-cell>
            <mt-cell title="证件号码"
                     :value="productsList.name"
                     is-link></mt-cell>
            <mt-cell title="账户可用余额"
                     :value="productsList.public_person"
                     is-link></mt-cell>
            <mt-cell title="起点金额"
                     :value="productsList.status"
                     is-link></mt-cell>
            <mt-cell title="递增金额"
                     :value="productsList.s_date"
                     is-link></mt-cell>
            <mt-cell title="首次购买最低金额"
                     :value="productsList.b_date"
                     is-link></mt-cell>
            <mt-field label="购买金额"
                      v-model="productsList.money"></mt-field>
            <mt-field label="交易密码"
                      v-model="productsList.pwd"></mt-field>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doNext">确定</mt-button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            productsList: {
                account: '4355345345435432',
                code: '934583429058045',
                public_person: '19394389.00',
                s_date: '1000',
                b_date: '1000',
                e_date: '5000',
                money: '',
                pwd: ''
            }
        }
    },
    methods: {

        doNext(item) {
            if (this.$validate.isEmpty(this.productsList.money)) {
                this.$msg('购买金额不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isAmount(this.productsList.money)) {
                this.$msg('购买金额格式错误', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(this.productsList.pwd)) {
                this.$msg('交易密码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.productsList.pwd)) {
                this.$msg('交易密码格式错误。', 'error', 1000);
                return false;
            }
            this.$router.push({ name: 'financialProductsMsg'});
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}
</script>


