<template>
    <div class="field-part">
        <div class="field-part">
            <mt-cell title="账户信息:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell v-for="(item, index) in dataForm" :key="index" v-if="showList(index)" :title="listMap[index]" v-model=" item "></mt-cell>
            <!--<mt-cell title="活期账户:" v-model=" dataForm.current "></mt-cell>
                <mt-cell title="定期账户:" v-model=" dataForm.regular "></mt-cell>
                <mt-cell title="账户余额:" v-model=" dataForm.balance "></mt-cell>
                <mt-cell title="币种:" v-model=" dataForm.currency "></mt-cell>
                <mt-cell title="账户状态:" v-model=" dataForm.status "></mt-cell>
                <mt-cell title="存期:" v-model=" dataForm.term "></mt-cell>
                <mt-cell title="起息日:" v-model=" dataForm.start_date "></mt-cell>
                <mt-cell title="到息日:" v-model=" dataForm.end_date "></mt-cell>
                <mt-cell title="执行利率:" v-model=" dataForm.rate "></mt-cell>-->
        </div>
    </div>
</template>

<script>
export default {
    props: ['dataCopy', 'map', 'filter'],
    data() {
        return {

        }
    },
    computed: {
        dataForm() {
            return this.dataCopy;
        },
        listMap() {
            return this.map;
        },
        filterList() {
            return this.filter;
        }
    },
    methods: {
        showList(index) {
            if (this.filterList.indexOf(index) != -1) {
                return false;
            }
            else {
                return true;
            }
        }
    }
}

</script>



