<template>
    <div>
        <do-msg :go-back="back"></do-msg>
    </div>
</template>

<script>
export default {
    data() {
        return {
            back: this.$route.params.go
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}
</script>



