<template>
    <div>
        <mt-cell v-for="item in list"
                 :key="item.herf"
                 :title="item.title"
                 :href="item.href"
                 is-link></mt-cell>
    </div>
</template>

<script>

export default {
    name: 'index',
    props: ['menuList'],
    data() {
        return {
            defaultList: [{
                title: "风险评估",
                href: "#/financial-products/assessment"
            }, {
                title: "理财交易",
                href: "#/financial-products/transfer"
            }, {
                title: "理财查询",
                href: "#/financial-products/query"
            }]
        }
    },
    computed: {
        list() {
            if (this.menuList === undefined || this.menuList === null) {
                return this.defaultList;
            }
            else {
                return this.menuList;

            }
        }
    }
}

</script>



