<template>
    <div>
        <div class="field-part">
            <mt-cell title="产品详细信息:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="产品代码"
                     :value="productsList.code"
                     is-link></mt-cell>
            <mt-cell title="产品名称"
                     :value="productsList.name"
                     is-link></mt-cell>
            <mt-cell title="发行人"
                     :value="productsList.public_person"
                     is-link></mt-cell>
            <mt-cell title="产品状态"
                     :value="productsList.status"
                     is-link></mt-cell>
            <mt-cell title="开始日期"
                     :value="productsList.s_date"
                     is-link></mt-cell>
            <mt-cell title="成立日期"
                     :value="productsList.b_date"
                     is-link></mt-cell>
            <mt-cell title="清盘日期"
                     :value="productsList.e_date"
                     is-link></mt-cell>
            <mt-cell title="年化收益率"
                     :value="productsList.yield"
                     is-link></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doNext">下一步</mt-button>
        </div>
    </div>
</template>

<script>
export default {
    data() {
        return {
            productsList: {
                name: '绯绯雨',
                code: 'fly001',
                public_person: '王五',
                s_date: '20160103',
                b_date: '20160201',
                e_date: '20160209',
                status: '认购期',
                yield: '20.00000%'
            }
        }
    },
    methods: {

        doNext(item) {
            this.$router.push({ name: 'financialProductsTransferSubscriptionDo', params: { info: item } });
        }
    },
    created() {
        
        console.log(this.$route.params.info);
        //this.productsList = Object.assign({}, this.$route.params.info);
        this.$store.dispatch('showLoading', false);
    }
}
</script>


