import * as types from '../mutation-types';

const state = {
    transferBankCard: {
        tranAmount: '',
        phone: '',
        remark: '',
        tranAccountId: '',
        tranAccount: ''
    }
};

// getters
const getters = {
    
};

// actions
const actions = {
    updateTransferBankCard: ({ commit }, data ) => {
        commit(types.UPDATE_TRANSFER_BANK_CARD, data);
    },
    clearTransferBankCard: ({ commit }, data ) => {
        commit(types.UPDATE_TRANSFER_BANK_CARD, data);
    },
};

// mutations
const mutations = {
    [types.UPDATE_TRANSFER_BANK_CARD] (state, data) {
        state.transferBankCard = data;
    },
    [types.CLEAR_TRANSFER_BANK_CARD] (state, data) {
        state.transferBankCard = {
            tranAmount: '',
            phone: '',
            remark: '',
            tranAccountId: '',
            tranAccount: ''
        };
    },
};

export default {
    state,
    getters,
    actions,
    mutations
};



