<template>
    <div>
        <account-query-info :data-copy="dataList" :map="listMap" :filter="filter" ></account-query-info>
    
        <div class="field-part">
            <mt-cell title="提取方式" v-model="infoData.type" @click.native="showTypeSheet = true"></mt-cell>
            <mt-field label="支取金额:" v-model="infoData.ammount" type="text"></mt-field>
            <mt-field label="证件号码:" v-model="infoData.idcard_No" type="text"></mt-field>
            <mt-field label="交易密码:" v-model="infoData.password" type="password"></mt-field>
        </div>
    
        <div class="field-action">
            <mt-button type="default" plain size="small" @click="$back">返回</mt-button>
            <mt-button type="primary" size="small" @click="doConfirm()">确定</mt-button>
        </div>
        <!--    提取方式弹出框    -->
        <mt-actionsheet :actions="typeSheetList" v-model="showTypeSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../../services/current-regular';
import AccountQueryInfo from '../account-query-info'
export default {
    components: {
        AccountQueryInfo
    },
    data() {
        return {
            dataList: {},
            listMap: {
                current: '活期账户:',
                regular: '定期账户:',
                balance: '账户余额:',
                currency: '币种:',
                status: '账户状态:',
                term: '存期:',
                start_date: '起息日:',
                end_date: '到息日:',
                rate: '执行利率:'
            },
            filter: ['id', 'type', 'state'],
            infoData: {
                type: '',
                ammount: '',
                idcard_No: '',
                password: ''
            },
            showTypeSheet: false,
            typeSheetListPart: [{
                name: '全部转活',
                method: this.typeCallback
            }],
            typeSheetListAll: [{
                name: '部分提取',
                method: this.typeCallback
            }, {
                name: '全部转活',
                method: this.typeCallback
            }],
            typeSheetList: [],
            id: ''
        }
    },
    methods: {
        doConfirm() {
            if (this.$validate.isEmpty(this.infoData.ammount)) {
                this.$msg('支取金额不能为空。', 'error', 1000);
                return false;
            }

            if (!this.$validate.isAmount(this.infoData.ammount)) {
                this.$msg('支取金额格式错误。', 'error', 1000);
                return false
            }
            if (this.$validate.isEmpty(this.infoData.idcard_No)) {
                this.$msg('证件号码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isIDcard(this.infoData.idcard_No)) {
                this.$msg('证件号码格式错误。', 'error', 1000);
                return false;
            }
            if (this.$validate.isEmpty(this.infoData.password)) {
                this.$msg('交易密码不能为空。', 'error', 1000);
                return false;
            }
            if (!this.$validate.isPwd(this.infoData.password)) {
                this.$msg('交易密码格式错误。', 'error', 1000);
                return false;
            }
            this.$router.push({ name: 'currentRegularMsg', params: { go: -3 } })
        },
        typeCallback(item) {
            this.infoData.type = item.name;
        }

    },
    created() {
        serviceAccount.getCurrentList()
            .then((res) => {
                let self = this;
                if (!res.data.status) {
                    Toast(res.data.message);
                    return;
                }
                let _data = res.data.list.filter((item) => {
                    return item.id === self.$route.query.id;
                })
                this.dataList = _data[0];
                self.id = this.dataList.type === "整存整取" ? "all" : "part";
                this.typeSheetList = this.id === 'all' ? this.typeSheetListAll : this.typeSheetListPart;
                this.infoData.type = this.typeSheetList[0].name;
            }, (error) => {
                Toast(err);
            })
    }
}

</script>



