<template>
    <div>
        <div class="field-part">
            <mt-cell title="一网通活期账户:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell :title="dataForm.name" v-model=" dataForm.tranAccount " @click.native=" showAccountSheet = true " is-link></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary" size="small" @click="doConfirm()">{{btnName}}</mt-button>
        </div>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet" v-model="showAccountSheet"></mt-actionsheet>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    props: ['pathName', 'btnName'],
    data() {
        return {
            dataForm: {
                tranAccount: '',
                tranAccountId: '',
                name: ''

            },
            accountListSheet: [],
            showAccountSheet: false,
        }
    },
    methods: {
        sheetCallback(item) {
            this.dataForm.tranAccount = item.name;
            this.dataForm.tranAccountId = item.id;
            this.dataForm.name = item.account_name;
        },

        doConfirm() {
            this.$router.push({ name: this.pathName, query: { id: this.dataForm.tranAccountId } })
        }
    },
    created() {
        serviceAccount.getAccountList().then((res) => {
            if (!res.data.status) {
                Toast(res.data.message);
                return;
            }
            let _firstAccount = res.data.list[0];

            if (!this.dataForm.tranAccount) {
                this.dataForm.tranAccount = _firstAccount.account;
                this.dataForm.tranAccountId = _firstAccount.id;
                this.dataForm.name = _firstAccount.account_name_copy;
            }

            let _temp = [];

            res.data.list.forEach((item, index) => {
                _temp.push({
                    id: item.id,
                    name: item.account,
                    account_name: item.account_name_copy,
                    method: this.sheetCallback,
                })
            });
            this.accountListSheet = _temp;

        }, (err) => {
            Toast(err);
        });
    }
}

</script>



