<template>
    <div>
        <div class="field-part">
            <mt-cell title="产品购买:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="理财账户:"
                     v-model=" infoData.account "
                     @click.native=" showAccountSheet = true "
                     is-link></mt-cell>
            <mt-field label="产品代码:"
                      v-model="infoData.code"
                      type="text"></mt-field>
            <mt-field label="产品名称:"
                      v-model="infoData.name"
                      type="text"></mt-field>
            <mt-cell title="投资期限:"
                     v-model="infoData.term"
                     @click.native=" showTermSheet = true "
                     is-link></mt-cell>
            <mt-cell title="产品状态:"
                     v-model="infoData.status"
                     @click.native=" showStateSheet = true "
                     is-link></mt-cell>
            <mt-cell title="是否保本:"
                     v-model="infoData.guaranteed"
                     @click.native=" showGuaranteedSheet = true "
                     is-link></mt-cell>
            <mt-cell title="预计年化收益率:"
                     v-model="infoData.yield"
                     @click.native=" showYieldSheet = true "
                     is-link></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="doConfirm">确定</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
        </div>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="accountListSheet"
                        v-model="showAccountSheet"></mt-actionsheet>
        <!--    投资期限弹出框    -->
        <mt-actionsheet :actions="termListSheet"
                        v-model="showTermSheet"></mt-actionsheet>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="stateListSheet"
                        v-model="showStateSheet"></mt-actionsheet>
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="guaranteedListSheet"
                        v-model="showGuaranteedSheet"></mt-actionsheet>
    
        <!--    账户弹出框    -->
        <mt-actionsheet :actions="yieldListSheet"
                        v-model="showYieldSheet"></mt-actionsheet>
    </div>
</template>

<script>
import Index from '../index'
import * as serviceAccount from '../../../services/account';
import * as serviceAccounts from '../../../services/products';
export default {
    data() {
        return {
            infoData: {
                account: '',
                accountId: '',
                code: '',
                name: '',
                term: '',
                status: '',
                guaranteed: '',
                yield: ''
            },
            accountListSheet: [],
            showAccountSheet: false,
            termListSheet: [],
            showTermSheet: false,
            stateListSheet: [],
            showStateSheet: false,
            guaranteedListSheet: [],
            showGuaranteedSheet: false,
            yieldListSheet: [],
            showYieldSheet: false
        }
    },
    methods: {
        doConfirm() {
            this.$router.push({ name: 'financialProductsTransferSubscriptionList' })
        },
        sheetCallback(item) {
            this.infoData.account = item.name;
            this.infoData.accountId = item.id;
        },
        termSheetCallback(item) {
            this.infoData.term = item.name;
        },
        stateSheetCallback(item) {
            this.infoData.state = item.name;
        },
        guaranteedSheetCallback(item) {
            this.infoData.guaranteed = item.name;
        },
        yieldSheetCallback(item) {
            this.infoData.yield = item.name;
        }
    },
    created() {
        Promise.all([serviceAccount.getAccountList(), serviceAccounts.getProductsSheetList()])
            .then((data) => {
                if (!data[0].data.status || !data[1].data.status) {
                    alert(data[0].data.message + '\r\n' + data[1].data.message);
                    return;
                }
                let _firstAccount = data[0].data.list[0];
                let _firstTerm = data[1].data.term[0];
                let _firstState = data[1].data.state[0];
                let _firstGuaranteed = data[1].data.guaranteed[0];
                let _firstYield = data[1].data.yield[0];

                if (!this.infoData.account) {
                    this.infoData.account = _firstAccount.account;
                    this.infoData.accountId = _firstAccount.id;
                }
                if (!this.infoData.term) {
                    this.infoData.term = _firstTerm.days;
                }
                if (!this.infoData.state) {
                    this.infoData.status = _firstState.status;
                }
                if (!this.infoData.guaranteed) {
                    this.infoData.guaranteed = _firstGuaranteed.is;
                }
                if (!this.infoData.yield) {
                    this.infoData.yield = _firstYield.status;
                }
                let _temp = [];
                let _temp2 = [];
                let _temp3 = [];
                let _temp4 = [];
                let _temp5 = [];

                data[0].data.list.forEach((item, index) => {
                    _temp.push({
                        id: item.id,
                        name: item.account,
                        method: this.sheetCallback,
                    })
                });
                data[1].data.term.forEach((item, index) => {
                    _temp2.push({
                        name: item.days,
                        method: this.termSheetCallback,
                    })
                });
                data[1].data.state.forEach((item, index) => {
                    _temp3.push({
                        name: item.status,
                        method: this.stateSheetCallback,
                    })
                });
                data[1].data.guaranteed.forEach((item, index) => {
                    _temp4.push({
                        name: item.is,
                        method: this.guaranteedSheetCallback,
                    })
                });
                data[1].data.yield.forEach((item, index) => {
                    _temp5.push({
                        name: item.status,
                        method: this.yieldSheetCallback,
                    })
                });
                this.accountListSheet = _temp;
                this.termListSheet = _temp2;
                this.stateListSheet = _temp3;
                this.guaranteedListSheet = _temp4;
                this.yieldListSheet = _temp5;


            }, (error) => {

            });


    }
}

</script>

<style lang="scss" scoped>
@import '../../../assets/styles/mixins';
</style>


