import axios from 'axios';

import api from './api';

export function getProductsList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getProductsList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}

export function getProductsSheetList() {
    return new Promise(function(resolve, reject) {
        axios.get(api.getUrl('getProductsSheetList')).then((res) =>{
            resolve(res);
        }, (res)=>{
            reject(res);
        }).catch((err)=>{
            reject(err);
        });
    });
}



