<template>
    <div>
       <do-msg :go-back="back"></do-msg>
    </div>
</template>

<script>
export default {
    data () {
        return {
            back: -3
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}

</script>



