<template>
    <div>
        <ul class="cell-container" v-for="item in list" @click="doDetail(item)">
            <li class="cell-wrap" v-for="(value, index) in item" v-if="showList(index)">
                <div class="cell-wrap-left">{{listMap[index]}}：</div>
                <div class="cell-wrap-right">
                    {{value}}
                </div>
            </li>
            <i class="mint-cell-allow-right"></i>
        </ul>
    </div>
</template>

<script>
/*  1、:cell-list传入数据 
 *  2、:map 传入label 
 *  3、:filter 过滤部分选项不显示 
 *  4、@callBack 按钮回掉事件 
 */
export default {
    props: ['cellList', 'map', 'filter'],
    data() {
        return {
           
        }
    },
    computed: {
        list() {
            return this.cellList
        },
        listMap() {
            return this.map;
        },
        filterList() {
            return this.filter;
        }
    },
    methods: {
        doDetail(item) {
            this.$emit('callBack', [item])
        },
        showList(index) {
            if(this.filterList.indexOf(index) != -1) {
                return false;
            }
            else {
                return true;
            }
        }
    }
}
</script>

<style lang="scss" scoped>
@import '../assets/styles/mixins';
$cell-color: #797474;
.cell-container {
    position: relative;
    display: block;
    overflow: hidden;
    box-sizing: border-box;
    background-color: #fff;
    color: inherit;
    min-height: 48px;
    text-decoration: none;
    @include mini-border(top, #d9d9d9, after);
    .mint-cell-allow-right {
        &:after {
            right: 60px;
        }
    }
    &:last-child {
        @include mini-border(bottom, #d9d9d9, before);
    }
    .cell-wrap {
        display: -webkit-box;
        display: -ms-flexbox;
        display: flex;
        background-size: 120% 1px;
        background-repeat: no-repeat;
        background-position: 0 0;
        background-origin: content-box;
        -webkit-box-align: center;
        -ms-flex-align: center;
        align-items: center;
        font-size: 16px;
        line-height: 1;
        min-height: inherit;
        overflow: hidden;
        padding: 0 10px;
        width: 100%;
        color: $cell-color;
        margin: 20px;
        .cell-wrap-left {
            flex: 1;
        }
        .cell-wrap-right {
            margin-right: 72px;
            margin-left: 30px;
        }
    }
}
</style>


