<template>
    <div>
        <div class="field-part">
            <mt-cell title="客户信息查询:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="客户风险承受等级:"
                     :value="dataForm.grade"
                     type="text"></mt-cell>
            <mt-cell title="风险等级有效截止日期:"
                     :value="dataForm.date"
                     type="text"></mt-cell>
            <mt-cell title="风险等级评估得分:"
                     :value="dataForm.scroll"
                     type="text"></mt-cell>
        </div>
        <div class="field-action">
            <mt-button type="primary"
                       size="small"
                       @click="$back">返回</mt-button>
            <mt-button type="primary"
                       size="small"
                       @click="doReassessment()">重新评估</mt-button>
        </div>
        <div class="reminder">
            <p class="reminder-title">温馨提示：</p>
            <p class="reminder-content">风险承受能力评估结果有效期为一年，请在结果失效前及时更新您的评估信息。 此评估仅供购买进入产品参考之用，其内容并未包含所有影响投资风险承受能力的因素。 如您需要对当前的风险承受能力进行重新评估，请填写《个人投资风险承受能力评估表》。
            </p>
        </div>
    </div>
</template>

<script>
import { Toast, Actionsheet } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../../services/account';
export default {
    data() {
        return {
            dataForm: {
                grade: '进取型',
                date: '20200908',
                scroll: '40'
            }
        }
    },
    methods: {
        doReassessment() {
            this.$router.push({ name: 'financialProductsReassessment' })
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}

</script>


