<template>
    <div>
        <!--  1、:cell-list传入数据 2、:map 传入label 3、:filter 过滤部分选项不显示 4、@callBack 按钮回掉事件 -->
        <list :cell-list="dataList" :map="listMap" :filter="filter" @callBack="doConfirm"></list>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../../services/current-regular';
export default {
    data() {
        return {
            dataList: [],
            filter: ['id', 'current', 'currency', 'status'],
            listMap: {
                type: '类型',
                regular: '子账户',
                balance: '余额',
                term: '期限',
                rate: '利率',
                start_date: '起息日',
                end_date: '到期日',
                state: '缓存状态'
            }
        }
    },
    methods: {
        doConfirm(item) {
            let val = item[0];
            this.$router.push({ name: 'regularCurrentListDo', query: { id: val.id } })
        }
    },
    created() {
        serviceAccount.getCurrentList()
            .then((res) => {
                let self = this;
                if (!res.data.status) {
                    Toast(res.data.message);
                    return;
                }
                this.dataList = res.data.list;

            }, (error) => {
                Toast(err);
            })
    }
}
</script>



