<template>
    <div>
        <div class="field-part">
            <mt-cell title="账户信息:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell title="活期账户:" v-model=" dataForm.account "></mt-cell>
            <mt-cell title="可用余额:" v-model=" dataForm.balance "></mt-cell>
            <mt-cell title="币种:" v-model=" dataForm.currency "></mt-cell>
        </div>
    </div>
</template>

<script>
import { Toast } from 'mint-ui-rem-dpr';
import * as serviceAccount from '../../services/account';
export default {
    props: ['id', 'type'],
    data() {
        return {
            dataForm: {}
        }
    },
    created() {
        serviceAccount.getAccountList()
            .then((res) => {
                let self = this;
                if (!res.data.status) {
                    Toast(res.data.message);
                    return;
                }
                let _data = res.data.list.filter((item) => {
                    return item.id === self.id;
                })
                _data[0].currency = this.type;
                this.dataForm = _data[0];
            }, (error) => {
                Toast(err);
            })
    }
}

</script>



