<template>
    <div>
        <div class="field-part">
            <mt-cell title="风险揭示书:"></mt-cell>
        </div>
        <div class="field-part">
            <mt-cell v-for="(index,value) in item"
                     :title="value"
                     :key="index"
                     is-link></mt-cell>
        </div>
        <div class="reminder">
            <p class="reminder-title">温馨提示：</p>
            <p>请先仔细阅读然后填写《个人投资风险承受能力评估表》。</p>
            <p>1.以下一系列问题可在您选择金融产品前，协助您评估自身的风险承受能力、理财方式及投资目标。</p>
            <p>2.您须认知、了解并同意，如您提供不准确或不完成资料，或选择不提供特定资料，则可能对您投资风险承受能力的评估及金融产品推荐带来影响，我行无任何责任。</p>
            <p>3.理财非存款，产品有风险，投资需谨慎。投资者在选择金融产品时须遵守具体产品的规定及条件，同时还需充分考虑投资可能面临的市场风险、汇率风险、信用风险以及可能的本金损失等风险。</p>
            <p>以下问题将根据您的财务状况、投资经验、投资偏好和风险承受能力对您进行风险评估，我们将根据评估结果为您更好的配置资产，请您认真作答，感谢您的配合！（每个问题请选择唯一选项，不可多选）</p>
        </div>
        <div>
            <p class="btn-title">您是否同意其中所有的条款并准备购买</p>
            <div class="field-action">
                <mt-button type="primary"
                           size="small"
                           @click="$back">我不同意</mt-button>
                <mt-button type="primary"
                           size="small"
                           @click="doAgree">我同意</mt-button>
            </div>
        </div>
        <div class="reminder">
            <p class="reminder-title">温馨提示：</p>
            <p class="reminder-content">风险承受能力评估结果有效期为一年，请在结果失效前及时更新您的评估信息。 此评估仅供购买进入产品参考之用，其内容并未包含所有影响投资风险承受能力的因素。 如您需要对当前的风险承受能力进行重新评估，请填写《个人投资风险承受能力评估表》。
            </p>
        </div>
    </div>
</template>

<script>
import { MessageBox  } from 'mint-ui-rem-dpr';
export default {
    data() {
        return {
            item: ['风险揭示书', '理财产品说明书', '客户权益须知', '理财产品销售协议书']
        }
    },
    methods: {
        doAgree() {
            MessageBox.confirm('您是否同意其中所有的条款并准备购买?').then( (action) => {
                this.$router.push({name: 'financialProductsTransferSubscriptionDetail', params: { info: this.$route.params.info } })
            }, (action) => {
                
            });
        }
    },
    created() {
        //console.log(this.$route.params.info);
        this.$store.dispatch('showLoading', false);
    }
}
</script>
<style lang="scss" scoped>
@import '../../../assets/styles/mixins';

.btn-title {
    text-align: center;
    margin: 20px auto;
}
</style>


