<template>
    <div>
        <do-msg :go-back="back"></do-msg>
    </div>
</template>

<script>
import DoMsg from '../../components/do-msg'
export default {
    components: {
        DoMsg
    },
    data() {
        return {
            back: -6
        }
    },
    created() {
        this.$store.dispatch('showLoading', false);
    }
}
</script>



